const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			formNode: {
				user_setting_account: '',
				user_setting_passwd: '',
				post_type: 'post',
				token: ''
			}
		}
	},
	onLoad(e) {
		console.log(e,'n')
		this.token = uni.getStorageSync('token')
		this.formNode.user_setting_account = e.item
	},
	methods: {
		getValue(e){
			let formNode = this.formNode
			let name = e.currentTarget.dataset.name
			let value = e.detail.value
			formNode[name] = value
			this.formNode = formNode
		},
		determine(){
			if(this.formNode.user_setting_account == ''){
				uni.showToast({
					title: '请输入账号',
					icon:'none'
				})
				return false
			}
			if(this.formNode.user_setting_passwd == ''){
				uni.showToast({
					title: '请输入密码',
					icon:'none'
				})
				return false
			}
			if(this.formNode.user_setting_passwd.length <'6'){
				uni.showToast({
					title: '请输入6位数密码',
					icon:'none'
				})
				return false
			}
			uni.request({
				url: `${helper.requestUrl}/promote-getmycode`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: this.formNode,
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res,'**');
					if (res.data.code == 0) {
						uni.showToast({
							title: res.data.tip_msg,
							icon: 'none'
						});
						setTimeout( e =>{
							uni.navigateBack({
								delta: 2
							})
						},2000)
					} else {
						uni.showToast({
							title: res.data.tip_msg,
							icon: 'none'
						});
					}
				}
			});
		}
	}
}
